html,
body {
  width: 100%;
  height: 100%;
}
#root {
  width: 100%;
  height: 100%;
}
.draw-home-wrap {
  background: #e4393c;
  width: 100%;
  height: 100%;
  position: relative;
}
.draw-home-wrap > div:first-child {
  width: 100%;
  height: 100%;
}
.draw-home-wrap .draw-list {
  width: 400px;
  height: 40px;
  position: absolute;
  left: 50%;
  top: 10%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 99;
}
.draw-home-wrap .draw-list p {
  font-size: 100px;
  text-align: center;
}
.draw-home-wrap .draw-content {
  min-width: 300px;
  padding: 20px !important;
  position: absolute;
  top: 70%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.draw-home-wrap .draw-content .btn-groups {
  margin: 10px 0;
}
.draw-home-wrap .draw-content .btn-groups button {
  color: #fff;
}
.look-list {
  height: 300px;
  overflow-y: scroll;
}
.look-list p {
  display: inline-block;
  width: 33.33%;
  text-align: center;
}
